<template>
  <v-card class="pa-1">
    <v-menu
      offset-y
      max-height="200px"
      style="float: right; margin-bottom: -999px; z-index: 999">
      <v-btn
        small
        flat
        color="primary"
        slot="activator"
        class="px-2">
          <i class="fa fa-history"></i> &nbsp; {{ increments[increment][1] }}
      </v-btn>
      <v-list dense>
        <v-list-tile v-for="(data, i) in increments" :key="i" @click="increment = i; update()">
          <v-list-tile-title>{{ data[1] }}</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-menu>
    <div v-if="state == 'loading'" class="text-xs-center pa-3">
      <v-progress-circular indeterminate color="primary"></v-progress-circular>
    </div>
    <div v-else-if="state == 'incomplete'" class="text-xs-center pa-3">
      Not enough data to display that time frame.
    </div>
    <div :style="'display:' + (state == 'done' ? 'block' : 'none')">
      <highcharts :options="chart" ref="chart"></highcharts>
    </div>
  </v-card>
</template>

<script>
  export default {
    name: 'statistics',
    props: ['title', 'info', 'id', 'type', 'machine_id'],
    data() {
      let vm = this;
      return {
        increment: (!localStorage['stats-' + this.id] ? (this.increment ? this.increment : 1) : localStorage['stats-' + this.id]),
        increments: [
          ['5m', '5 minutes'],
          ['10m', '10 minutes'],
          ['30m', '30 minutes'],
          ['1hr', 'Hourly'],
          ['6hr', '6 hours'],
          ['12hr', '12 hours'],
          ['1d', 'Daily'],
          ['7d', 'Weekly']
        ],

        state: 'loading',
        loops: [],

        total: null,
        tag: '',

        chart: {
          chart: {
            type: 'area',
            height: 250
          },
          legend: {
            enabled: false,
          },
          credits: {
            enabled: false
          },
          title: {
            text: this.title
          },
          subtitle: {
            text: this.info
          },
          xAxis: {
            type: 'datetime',
            labels: {
              enabled: false
            }
          },
          yAxis: {
            title: 'Loading',
            labels: {
              formatter: function () {
                return this.value + ' ' + vm.tag;
              }
            }
          },
          series: [{
            type: 'area',
            name: this.title,
            color: {
              linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
              stops: [
                  [0, 'rgb(244, 67, 54, .8)'],
                  [1, 'rgb(255, 80, 40, .4)']
              ]
            },
            data: [0, 1, 15],
            threshold: null
          }]
        }
      }
    },
    computed: {
      statistics() {
        return this.$store.state.statistics[this.id];
      }
    },
    watch: {
      increment(val, oldVal) {
        localStorage['stats-' + this.id] = val;

        this.initLoop();
      },
      'statistics':  {
        handler(val, oldVal) {
          let is_incomplete = true;
          let max_amt = [0, ''];
          let series = [];

          this.total = 0;

          for(let i in val) {
            // if(i == val.length - 1) continue;
            let [date, stats] = val[i];
            let column = [null, null];
            series.push(column);

            date = new Date(date * 1000);

            if(this.increment > 5)
              column[0] = date.toLocaleDateString([], { month: 'short', day: 'numeric' });
            else
              column[0] = date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });

            if(stats == null)
              continue;
            
            is_incomplete = false;

            if(this.type == 'gpus') {
              column[1] = stats['gpus'] > 0 ? stats['gpus'] : null;
              this.total += stats['gpus'];
            }else if(this.type == 'online') {
              column[1] = stats['online'] > 0 ? stats['online'] : null;
              this.total += stats['online'];
            }else if(this.type == 'offline') {
              column[1] = stats['offline'] > 0 ? stats['offline'] : null;
              this.total += stats['offline'];
            }else if(this.type == 'rate') {
              if(stats['rate'] < 0) {
                console.error('Rate is a negative number!');
                continue;
              }

              if(stats['rate'] > max_amt[0]) {
                max_amt = this.convertRate({ rate: stats['rate'] });
                max_amt[0] = stats['rate'];
              }

              column[1] = stats['rate'] > 0 ? stats['rate'] : null;
            }

            else if(this.type == 'invalid') {
              if(stats['shares']['invalid'] < 0) {
                console.error('Invalid share count is a negative number!');
                continue;
              }

              column[1] = stats['shares']['invalid'] > 0 ? stats['shares']['invalid'] : null;
              this.total += stats['shares']['invalid'];
            }else if(this.type == 'accepted') {
              if(stats['shares']['accepted'] < 0) {
                console.error('Accepted share count is a negative number!');
                continue;
              }

              column[1] = stats['shares']['accepted'] > 0 ? stats['shares']['accepted'] : null;
              this.total += stats['shares']['accepted'];
            }else if(this.type == 'rejected') {
              if(stats['shares']['rejected'] < 0) {
                console.error('Rejected share count is a negative number!');
                continue;
              }

              column[1] = stats['shares']['rejected'] > 0 ? stats['shares']['rejected'] : null;
              this.total += stats['shares']['rejected'];
            }
          }

          if(this.total == 0)
            this.total = null;

          if(this.type == 'rate') {
            this.tag = ' ' + max_amt[1] + '/s';
            for(let i in series) {
              if(series[i][1] == null) continue;
              series[i][1] = parseFloat(this.convertRate({ rate: series[i][1], display: false, minimum: max_amt[1] })[0].toFixed(2));
            }
          }

          this.$refs.chart.chart.series[0].setData(series);

          this.state = is_incomplete ? 'incomplete' : 'done';
        },
        deep: true
      }
    },
    methods: {
      initLoop() {
        let vm = this;

        for(let loop of this.loops)
          clearInterval(loop);
        
        this.loops = [];

        this.loops.push(setInterval(function() {
          vm.update(true);
        }, 1000 * 60 * 2.5));

        this.loops.push(setInterval(function() {
          vm.$refs.chart.chart.reflow();
        }, 500));
      },
      update(silent=false) {
        if(!silent) {
          this.state = 'loading';
          this.total = null;
        }

        this.$socket.send('stats', 'query', {
          'id': this.id,
          'increment': this.increments[this.increment][0],
          machine_id: this.machine_id
        });
      }
    },
    mounted() {
      this.initLoop();

      this.update();
    },
    destroyed() {
      for(let loop of this.loops)
        clearInterval(loop);
    }
  }
</script>



